* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-size: 16px;
    font-family: 'Open Sans', sans-serif;
    background-color: #ecf0f1;
    color: #2c3e50;
    padding: 100px 0% 50px 0%;
    /* display: flex; */
    /* justify-content: center; */
}

/* #root,
.App {
    width: 100%;
} */

table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent;
}

h1 {
    font-size: 2rem;
}
h3 {
    font-size: 1.25rem;
    letter-spacing: 0.2rem;
}
h4 {
    font-size: 1.1rem;
    font-weight: normal;
}

.totalWrapper {
    margin-top: 1.2em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2em;
}

@media (max-width: 767px) {
    body {
        padding: 50px 15px 30px 15px;
    }
    h1 {
        font-size: 1.5rem;
    }
    h3 {
        font-size: 1.1rem;
    }
    h4 {
        font-size: 1rem;
    }

    .totalWrapper {
        grid-template-columns: none;
        grid-template-rows: 1fr 1fr;
        grid-gap: 0.75em;
    }
}

