.totalCard {
    background-color: white;
    border-radius: 0.5em;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    padding: 1em 1.25em;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
}

.totalCard .desc {
    flex-grow: 1;
}

.totalCard .title {
    font-size: 0.8rem;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.5);
    text-transform: uppercase;
}

.totalCard .figure {
    font-size: 2rem;
    font-weight: bold;
}

.confirmedFigure {
    color: #3498db;
}

.deathsFigure {
    color: #e74c3c;
}

.totalCard .confirmedIcon,
.totalCard .deathsIcon {
    align-self: stretch;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
}

.totalCard .confirmedIcon {
    font-size: 2rem;
    color: #3498db;
}

.totalCard .deathsIcon {
    font-size: 2rem;
    color: #e74c3c;
}

.tableWrapper {
    margin-top: 2em;
}

.table {
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    font-size: 0.9rem;
    text-align: center;
}

.td {
    padding: 0.25em 1em;
}

.th {
    font-weight: bold;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    background-color: #2c3e50 !important;
    color: white;
    padding: 0.2em 0;
}

.tr {
    display: grid;
    grid-gap: 1em;
    grid-template-columns: 1fr 3fr 2fr 2fr;
    border-radius: 0.5em;
    transition: all 0.2s ease-in-out;
}

@media (max-width: 767px) {
    .tr {
        font-size: 0.7rem;
    }

    .td {
        text-overflow: ellipsis;
    }
}

.tr:nth-child(odd) {
    background-color: white;
}

.tr:hover {
    background-color: #7f8c8d;
    color: white;
}

.tr .td:nth-child(2) {
    text-align: start;
}

.loaderWrapper {
    background: white;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.loaderWrapper p {
    margin-top: 1.5em;
}

.loader {
    border: 16px solid #f3f3f3;
    border-radius: 50%;
    border-top: 16px solid #3498db;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.alertMessage {
    min-width: 200px;
    justify-content: space-between;
}
